import Vue from 'vue';
import {createRouter} from './router/index';
import home from './views/home.vue';
import { sync } from 'vuex-router-sync'

export function createApp(){
    const router=createRouter();

    //sync(router);


    const app=new Vue({
       router,
       render:(h)=>h(home) 
    });

    return {app,router};
}