
import Vue from 'vue';
import Router from 'vue-router';
import home from '../views/home.vue';
import articleDetial from '../views/articleDetial.vue';



Vue.use(Router);

export function createRouter(){

    return new Router({
        mode:"history",
        routes:[
            {path:"/",component:home},
            {path:"/articleDetial",component:articleDetial}
        ]
    })

}